import React, { Component } from 'react';
//import { Link } from 'react-router-dom';

import Categories from '../categories';
//import Item from '../item';
import CarouselSlider from '../carousel-slider';
import Header from '../header';

import './home-page.sass';

export default class HomePage extends Component {

  constructor() {
    super();
    this.state = {
      dropdownIsHidden: true,
    };
  }

  updateData = (value) => {
     this.setState({ dropdownIsHidden: value })
  }

  render() {
    const arrayOfFive = [1, 2, 3, 4, 5];
    return (
      <div>
        <Header updateData={this.updateData}/>
        <div className="page-wrapper">
          <main className="page">
            <div className="row">
              <div className="col-12 col-lg-3">
                <Categories />
              </div>
              <div className="col-lg-9 small-delete">
                <CarouselSlider />
              </div>
            </div>
            <div className="col-12 small-delete">
              <article className="items-row">
                <h3>Рекомендуемые товары</h3>
                <div className="items-in-row">
                  {arrayOfFive.map((item) => {
                    return (
                      <div key={item} className="item-in-row">
                        item
                      </div>
                    )
                  })}
                </div>
              </article>
            </div>
          </main>
        </div>
      </div>
    );
  }
}
